import React, {useState} from "react";
import Layout from "./Layout";

const styleSheet = {
  container: {
    background: "black",
    height: "1000px",
    color: "white",
    textAlign: "center",
    paddingTop: "30px",
    fontSize: "4em"
  }
};
const View = ({data}) => {
  const [lyrics, setLyrics] = useState(data);
  useEffect(()=>{
    console.log(lyrics)
  },[])
  return (
    <Layout title="View">
      <div style={styleSheet.container}>View</div>
    </Layout>
  );
};

export default View;
